function splitText(id) {
  let el = document.getElementById("text");
  if (el)
    el.innerHTML = el.innerHTML
      .split(" ")
      .map(
        (word) =>
          `<div style="display: inline-block; position: relative;">${word
            .split("")
            .map(
              (c) =>
                `<div style="display: inline-block; position: relative;">${c}</div>`
            )
            .join("")}</div>`
      )
      .join(" ");

  let chars = Array.from(el.children).map((e) => Array.from(e.children));
  return {
    el,
    chars,
  };
}
export default App;
